import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Button,
  TextField,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Collapse,
  IconButton,
  Chip,
  Avatar,
  CircularProgress,
  Alert,
  Stack
} from '@mui/material';
import {
  ArrowBack as ArrowBackIcon,
  ExpandMore as ExpandMoreIcon,
  ExpandLess as ExpandLessIcon,
  Code as CodeIcon,
  Merge as PullRequestIcon,
  RateReview as ReviewIcon,
  Commit as CommitIcon,
  Add as AddIcon,
  Remove as RemoveIcon,
  OpenInNew as OpenInNewIcon
} from '@mui/icons-material';
import moment from 'moment';

const RepositoryAnalyticsPage = () => {
  const { repositoryId } = useParams();
  const navigate = useNavigate();
  
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [startDate, setStartDate] = useState(moment().subtract(30, 'days'));
  const [endDate, setEndDate] = useState(moment());
  const [expandedContributor, setExpandedContributor] = useState(null);

  const fetchAnalytics = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      
      const token = localStorage.getItem('auth_token');
      const params = new URLSearchParams({
        startDate: startDate.format('YYYY-MM-DD'),
        endDate: endDate.format('YYYY-MM-DD')
      });
      
      const response = await fetch(`/api/repository-analytics/${repositoryId}/contributors?${params}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to fetch repository analytics');
      }
      
      const analyticsData = await response.json();
      setData(analyticsData);
    } catch (err) {
      console.error('Error fetching repository analytics:', err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }, [repositoryId, startDate, endDate]);

  useEffect(() => {
    if (repositoryId) {
      fetchAnalytics();
    }
  }, [repositoryId, fetchAnalytics]);

  const handleDateRangeUpdate = () => {
    fetchAnalytics();
  };

  const handleContributorToggle = (contributorId) => {
    setExpandedContributor(expandedContributor === contributorId ? null : contributorId);
  };

  const formatDate = (dateString) => {
    return moment(dateString).format('MMM DD, YYYY');
  };

  const getPRStateColor = (state, merged) => {
    if (merged) return 'success';
    if (state === 'open') return 'primary';
    return 'default';
  };

  const getPRStateText = (state, merged) => {
    if (merged) return 'Merged';
    return state.charAt(0).toUpperCase() + state.slice(1);
  };

  if (loading) {
    return (
      <Container maxWidth="lg">
        <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
          <CircularProgress />
        </Box>
      </Container>
    );
  }

  if (error) {
    return (
      <Container maxWidth="lg">
        <Box py={4}>
          <Button
            startIcon={<ArrowBackIcon />}
            onClick={() => navigate('/repositories')}
            sx={{ mb: 2 }}
          >
            Back to Repositories
          </Button>
          <Alert severity="error">{error}</Alert>
        </Box>
      </Container>
    );
  }

  if (!data) {
    return (
      <Container maxWidth="lg">
        <Box py={4}>
          <Button
            startIcon={<ArrowBackIcon />}
            onClick={() => navigate('/repositories')}
            sx={{ mb: 2 }}
          >
            Back to Repositories
          </Button>
          <Alert severity="info">No data available</Alert>
        </Box>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg">
        <Box py={4}>
          {/* Header */}
          <Box display="flex" justifyContent="space-between" alignItems="center" mb={4}>
            <Box>
              <Button
                startIcon={<ArrowBackIcon />}
                onClick={() => navigate('/repositories')}
                sx={{ mb: 2 }}
              >
                Back to Repositories
              </Button>
              <Typography variant="h4" component="h1">
                {data.repository.name}
              </Typography>
              <Typography variant="body1" color="text.secondary">
                {data.repository.fullName}
              </Typography>
              {data.repository.description && (
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                  {data.repository.description}
                </Typography>
              )}
            </Box>
            {data.repository.language && (
              <Chip 
                label={data.repository.language} 
                color="primary" 
                variant="outlined"
              />
            )}
          </Box>

          {/* Date Range Selector */}
          <Card sx={{ mb: 4 }}>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Date Range Filter
              </Typography>
              <Stack direction="row" spacing={2} alignItems="center">
                <TextField
                  label="Start Date"
                  type="date"
                  value={startDate.format('YYYY-MM-DD')}
                  onChange={(e) => setStartDate(moment(e.target.value))}
                  variant="outlined"
                  size="small"
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
                <TextField
                  label="End Date"
                  type="date"
                  value={endDate.format('YYYY-MM-DD')}
                  onChange={(e) => setEndDate(moment(e.target.value))}
                  variant="outlined"
                  size="small"
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
                <Button 
                  variant="contained" 
                  onClick={handleDateRangeUpdate}
                  disabled={loading}
                >
                  Update
                </Button>
              </Stack>
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                Showing data from {startDate.format('MMM DD, YYYY')} to {endDate.format('MMM DD, YYYY')}
              </Typography>
            </CardContent>
          </Card>

          {/* Summary Cards */}
          <Grid container spacing={3} sx={{ mb: 4 }}>
            <Grid item xs={12} md={2}>
              <Card>
                <CardContent>
                  <Box display="flex" alignItems="center" mb={1}>
                    <PullRequestIcon color="primary" sx={{ mr: 1 }} />
                    <Typography variant="h6">PRs</Typography>
                  </Box>
                  <Typography variant="h4">{data.summary.totalPRs}</Typography>
                  <Typography variant="body2" color="text.secondary">
                    {data.summary.totalMerged} merged
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={2}>
              <Card>
                <CardContent>
                  <Box display="flex" alignItems="center" mb={1}>
                    <CommitIcon color="warning" sx={{ mr: 1 }} />
                    <Typography variant="h6">Commits</Typography>
                  </Box>
                  <Typography variant="h4">{data.summary.totalCommits}</Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={2}>
              <Card>
                <CardContent>
                  <Box display="flex" alignItems="center" mb={1}>
                    <AddIcon color="success" sx={{ mr: 1 }} />
                    <Typography variant="h6">Lines+</Typography>
                  </Box>
                  <Typography variant="h4">{data.summary.totalLinesAdded.toLocaleString()}</Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={2}>
              <Card>
                <CardContent>
                  <Box display="flex" alignItems="center" mb={1}>
                    <RemoveIcon color="error" sx={{ mr: 1 }} />
                    <Typography variant="h6">Lines-</Typography>
                  </Box>
                  <Typography variant="h4">{data.summary.totalLinesRemoved.toLocaleString()}</Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={2}>
              <Card>
                <CardContent>
                  <Box display="flex" alignItems="center" mb={1}>
                    <ReviewIcon color="info" sx={{ mr: 1 }} />
                    <Typography variant="h6">Reviews</Typography>
                  </Box>
                  <Typography variant="h4">{data.summary.totalReviews}</Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={2}>
              <Card>
                <CardContent>
                  <Box display="flex" alignItems="center" mb={1}>
                    <CodeIcon color="secondary" sx={{ mr: 1 }} />
                    <Typography variant="h6">Contributors</Typography>
                  </Box>
                  <Typography variant="h4">{data.totalContributors}</Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>

          {/* Contributors Table */}
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Individual Contributor Statistics
              </Typography>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Contributor</TableCell>
                      <TableCell align="center">PRs Opened</TableCell>
                      <TableCell align="center">PRs Merged</TableCell>
                      <TableCell align="center">Merge Rate</TableCell>
                      <TableCell align="center">Lines Added</TableCell>
                      <TableCell align="center">Lines Removed</TableCell>
                      <TableCell align="center">Reviews Given</TableCell>
                      <TableCell align="center">Reviews Received</TableCell>
                      <TableCell align="center">Commits</TableCell>
                      <TableCell align="center">Details</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {data.contributors.map((contributorData) => (
                      <React.Fragment key={contributorData.contributor.id}>
                        <TableRow hover>
                          <TableCell>
                            <Box display="flex" alignItems="center">
                              <Avatar 
                                src={contributorData.contributor.avatarUrl} 
                                sx={{ width: 32, height: 32, mr: 2 }}
                              >
                                {contributorData.contributor.username.charAt(0).toUpperCase()}
                              </Avatar>
                              <Box>
                                <Typography variant="body2" fontWeight="medium">
                                  {contributorData.contributor.name || contributorData.contributor.username}
                                </Typography>
                                <Typography variant="caption" color="text.secondary">
                                  @{contributorData.contributor.username}
                                </Typography>
                              </Box>
                            </Box>
                          </TableCell>
                          <TableCell align="center">{contributorData.stats.pullRequests.opened}</TableCell>
                          <TableCell align="center">{contributorData.stats.pullRequests.merged}</TableCell>
                          <TableCell align="center">{contributorData.stats.pullRequests.mergeRate}%</TableCell>
                          <TableCell align="center">
                            <Typography color="success.main">
                              +{contributorData.stats.linesOfCode.added.toLocaleString()}
                            </Typography>
                          </TableCell>
                          <TableCell align="center">
                            <Typography color="error.main">
                              -{contributorData.stats.linesOfCode.removed.toLocaleString()}
                            </Typography>
                          </TableCell>
                          <TableCell align="center">{contributorData.stats.reviews.given}</TableCell>
                          <TableCell align="center">{contributorData.stats.reviews.received}</TableCell>
                          <TableCell align="center">{contributorData.stats.commits.total}</TableCell>
                          <TableCell align="center">
                            <IconButton
                              onClick={() => handleContributorToggle(contributorData.contributor.id)}
                              disabled={contributorData.pullRequests.length === 0}
                            >
                              {expandedContributor === contributorData.contributor.id ? 
                                <ExpandLessIcon /> : <ExpandMoreIcon />
                              }
                            </IconButton>
                          </TableCell>
                        </TableRow>
                        
                        {/* Expandable PR Details */}
                        <TableRow>
                          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={10}>
                            <Collapse 
                              in={expandedContributor === contributorData.contributor.id} 
                              timeout="auto" 
                              unmountOnExit
                            >
                              <Box sx={{ margin: 2 }}>
                                <Typography variant="h6" gutterBottom>
                                  Pull Requests by {contributorData.contributor.name || contributorData.contributor.username}
                                </Typography>
                                {contributorData.pullRequests.length === 0 ? (
                                  <Typography variant="body2" color="text.secondary">
                                    No pull requests in this time period
                                  </Typography>
                                ) : (
                                  <Table size="small">
                                    <TableHead>
                                      <TableRow>
                                        <TableCell>PR #</TableCell>
                                        <TableCell>Title</TableCell>
                                        <TableCell>Status</TableCell>
                                        <TableCell>Lines +/-</TableCell>
                                        <TableCell>Created</TableCell>
                                        <TableCell>Closed/Merged</TableCell>
                                        <TableCell>Link</TableCell>
                                      </TableRow>
                                    </TableHead>
                                    <TableBody>
                                      {contributorData.pullRequests.map((pr) => (
                                        <TableRow key={pr.id}>
                                          <TableCell>#{pr.number}</TableCell>
                                          <TableCell>
                                            <Typography variant="body2" noWrap sx={{ maxWidth: 300 }}>
                                              {pr.title}
                                            </Typography>
                                          </TableCell>
                                          <TableCell>
                                            <Chip 
                                              label={getPRStateText(pr.state, pr.merged)}
                                              color={getPRStateColor(pr.state, pr.merged)}
                                              size="small"
                                            />
                                          </TableCell>
                                          <TableCell>
                                            <Box>
                                              <Typography variant="body2" color="success.main">
                                                +{pr.additions}
                                              </Typography>
                                              <Typography variant="body2" color="error.main">
                                                -{pr.deletions}
                                              </Typography>
                                            </Box>
                                          </TableCell>
                                          <TableCell>{formatDate(pr.createdAt)}</TableCell>
                                          <TableCell>
                                            {pr.mergedAt ? formatDate(pr.mergedAt) : 
                                             pr.closedAt ? formatDate(pr.closedAt) : '-'}
                                          </TableCell>
                                          <TableCell>
                                            <IconButton
                                              size="small"
                                              onClick={() => window.open(pr.htmlUrl, '_blank')}
                                            >
                                              <OpenInNewIcon fontSize="small" />
                                            </IconButton>
                                          </TableCell>
                                        </TableRow>
                                      ))}
                                    </TableBody>
                                  </Table>
                                )}
                              </Box>
                            </Collapse>
                          </TableCell>
                        </TableRow>
                      </React.Fragment>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </CardContent>
          </Card>
        </Box>
      </Container>
  );
};

export default RepositoryAnalyticsPage;